                tanggal_lahir: '',
                alamat: '',
                nama_wali: '',
                jenis_kelamin: '',
                jumlah_kehamilan: null,
                pernah_abortus: '',
                pernah_hdn: '',
                golongan_darah: '',
                rhesus: '',
                komponen_id: null,
                jumlah_kantong: 1,
                jumlah_cc: '',
                diagnosis_klinis: '',
                alasan_transfusi: '',
                kadar_hb: null,
                status: 1,
                status_keterangan: 'Menunggu Diperiksa oleh Admin UPD',
                transfusi_sebelumnya: 'Tidak',
                transfusi_kapan: '',
                reaksi_transfusi: 'Tidak',
                gejala_transfusi: '',
                coomb_test: 'Tidak',
